@import "../../bower_components/bootstrap-sass/assets/stylesheets/bootstrap";

@mixin blueText {
    color: #00b3ee; }

body {
    background: #fdfbff; }

.content {
    @extend .row;
    @include blueText; }

.event {
    @extend .col-md-3;
    @extend .col-xs-12;
    @extend .col-sm-4; }

input {
    &.submit {
        @extend .btn;
        @extend .btn-primary; } }

.half {
    @extend .col-md-4; }

